<template>
  <div id="groupfees">
    <Header>
      <el-breadcrumb separator-class="el-icon-arrow-right" slot="left">
        <el-breadcrumb-item>预算管理</el-breadcrumb-item>
        <el-breadcrumb-item>费用预算</el-breadcrumb-item>
      </el-breadcrumb>
      <div slot="right">
      </div>
    </Header>
    <div class="content">
      <el-row>
        <el-col :span="4">
          <el-date-picker v-model="yearValue" type="year" placeholder="选择年" @change="selectYearData"  format="yyyy 年" value-format="yyyy"></el-date-picker>
        </el-col>
        <el-col :span="8" :offset="1">
          <el-input placeholder="输入编号或预算体查询" v-model="keywords" @change="search">
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="tableData" border style="width: 100%; margin-top: 20px">
        <el-table-column prop="code" label="编号" width="180"> </el-table-column>
        <el-table-column prop="name" label="预算体"></el-table-column>
        <el-table-column label="费用类型">
          <template slot-scope="scope">
            {{feeType[scope.row.type]}}
          </template>
        </el-table-column>
        <el-table-column pr prop="budget" label="预算"></el-table-column>
        <el-table-column pr prop="expense" label="己用"></el-table-column>
        <el-table-column pr prop="trip" label="余额"></el-table-column>
      </el-table>

      <el-pagination :current-page="page" :page-size="15" layout="total, prev, pager, next, jumper" :total="count" align="right" @current-change="changePage" class="pagi">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  export default {
    name: 'organization',
    components: {
      Header
    },
    data() {
      return {
        tableData: [],
        page: 1,
        limit: 5,
        count: 0,
        year: new Date().getFullYear(),
        feeType: {
          trip: '差旅费',
          benefits: '福利费',
          others: '其他费用'
        },
        keywords: '',
        yearValue: new Date(),
      }
    },
    methods: {
      search() {
        this.page = 1;
        this.keywords = this.keywords.trim();
        this.getGroupBudgets()
      },
      selectYearData() {
        this.page = 1;
        this.year = new Date(this.yearValue).getFullYear();
        this.getGroupBudgets()
      },
      getGroupBudgets() {
        this.$http.get(`/ec/api/budgets/lists?year=${this.year}&page=${this.page}&limit=${this.limit}&keywords=${this.keywords}`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            this.count = (resData.data.count || 0) * 3;
            this.setData(resData.data.budgets);
          }
        })
      },
      setData(budgets = []) {
        let row = [];
        let typeArray = ['trip', 'benefits', 'others']
        budgets.map(item => {
          for (let type of typeArray) {
            row.push({
              code: item.code,
              name: item.name,
              type,
              budget: item[type].budget,
              expense: item[type].expense,
              trip: item[type].balance,
            })
          }
        })

        this.tableData = row;
      },
      changePage(page) {
        this.page = page;
        this.getGroupBudgets()
      }
    },
    created() {
      this.page = this.$route.query.page || 1;
      this.getGroupBudgets()
    }
  }
</script>

<style>
.pagi {
  margin: 10px;
}
</style>


